import { Component } from '@angular/core';
import { SectionType } from '@core/enums';
import { Section } from '@core/models';
import { PlaceAdFacade } from '@feature/place-ad/place-ad.facade';
import { Observable } from 'rxjs';
@Component({
    selector: 'findqo-place-ad-category',
    templateUrl: './place-ad-category.page.html',
    styleUrl: './place-ad-category.page.scss',
    standalone: false
})
export class PlaceAdCategoryPage {
	constructor(private placeAdFacade: PlaceAdFacade) {
		this.sectionList$ = placeAdFacade.getSectionList$();
	}

	sectionList$: Observable<Section[]>;
	readonly sectionTypes = SectionType;
}
