import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteDataType } from '@core/services/route.service';
import { PlaceAdCategoryPage } from './place-ad-category.page';

const routes: Routes = [
	{
		path: '',
		component: PlaceAdCategoryPage,
		data: {
			[RouteDataType.IS_HEADER_VISIBLE]: false,
			[RouteDataType.IS_FOOTER_VISIBLE]: false,
		},
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class PlaceAdCategoryRoutingModule {}
